.box{
    background: white;
    width: 100%;
    height: 700px;
    display: flex;
    align-items: center;
    flex-direction: column;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3), 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 10px; /* 테두리를 둥글게 하는 border-radius 속성 */
}
.b{
    border: none;
}